<template>
  <div>
    <Head title="购物车" :show="true"></Head>
    <h2>{{count}}</h2>
    <h2>{{newCount}}</h2>
    <h2>{{city}}</h2>
    <van-button type="primary" @click="addCount">count+</van-button>
    <!-- <van-button type="primary" @click="newCount">count*</van-button> -->
    <van-button type="warning" @click="changeCity('成都')">城市</van-button>
  </div>
</template>
<script>
export default {
  computed:{
    count(){
      return this.$store.state.count+10
    },
    newCount(){
      return this.$store.getters.newCount;
    },
    city(){
      return this.$store.state.city
    }
  },
  methods:{
    addCount(){
      this.$store.commit("addCount",50);
    },
    changeCity(city){
      this.$store.commit("changeCity",city);
    }
  }
}
</script>
